html {
    font-family: Roboto, 'Open Sans', Helvetica Neue, sans-serif;
}

html, body {
    overflow-x: hidden;
}

body {
    position: relative;
}

h1, h2, h3, h4, h5, h6 {
}

ul{
    list-style: none;
}

a {
    cursor: pointer;
    color: inherit;
    text-decoration: none;
}

button {
    cursor: pointer;
    border: none;
}

p {
    
}

::-webkit-scrollbar {
    width: em(10px);
}

/* Track */
::-webkit-scrollbar-track {
    background: #1f1f1f; 
}

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 0.5em;
    background: #444; 
    box-shadow: inset 0 0 5px 0 rgba(255, 255, 255, 0.4);
    transition: all 0.5s;
    
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgba(102, 102, 102, 0.8);
  }